import { useEffect, useState } from "react";
import Image from "next/image";

interface Post {
  id: number;
  content: string;
  user_id: number;
  image_url?: string;
  video_url?: string;
  likes: number;
  dislikes: number;
  funny: number;
  hates: number;
}

export default function Home() {
  const [posts, setPosts] = useState<Post[]>([]);

  useEffect(() => {
    fetch(`${process.env.NEXT_PUBLIC_API_URL}/posts/feed`)
      .then((response) => response.json())
      .then((data) => setPosts(data));
  }, []);

  return (
    <div className="min-h-screen bg-gray-100 text-gray-900 dark:bg-gray-900 dark:text-gray-100">
      <header className="bg-white dark:bg-gray-800 shadow-md p-4 flex items-center justify-between">
        <h1 className="text-xl font-bold">Trilhagram</h1>
        <nav className="flex gap-4">
          <a href="#" className="hover:underline">
            Home
          </a>
          <a href="#" className="hover:underline">
            Profile
          </a>
          <a href="#" className="hover:underline">
            Settings
          </a>
        </nav>
      </header>

      <main className="p-4 sm:p-8">
        <div className="max-w-2xl mx-auto space-y-6">
          {posts.map((post) => (
            <div
              key={post.id}
              className="bg-white dark:bg-gray-800 p-4 rounded-lg shadow-md"
            >
              <div className="flex items-center gap-4">
                <Image
                  src="/default-avatar.png" // Substitua por uma imagem real do usuário, se disponível
                  alt="User Avatar"
                  width={40}
                  height={40}
                  className="rounded-full"
                />
                <div>
                  <h2 className="font-bold">User {post.user_id}</h2>
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    Post ID: {post.id}
                  </p>
                </div>
              </div>
              <p className="mt-4">{post.content}</p>
              {post.image_url && (
                <Image
                  src={`${process.env.NEXT_PUBLIC_API_URL}${post.image_url}`}
                  alt="Post Image"
                  width={500}
                  height={300}
                  className="rounded-lg mt-4"
                />
              )}
              <div className="mt-4 flex gap-4">
                <button className="text-blue-500 hover:underline">
                  Like ({post.likes})
                </button>
                <button className="text-red-500 hover:underline">
                  Dislike ({post.dislikes})
                </button>
                <button className="text-blue-500 hover:underline">
                  Funny ({post.funny})
                </button>
                <button className="text-red-500 hover:underline">
                  Hate ({post.hates})
                </button>
              </div>
            </div>
          ))}
        </div>
      </main>

      <footer className="bg-white dark:bg-gray-800 p-4 text-center shadow-md">
        <p>© 2025 Trilhagram. All rights reserved.</p>
      </footer>
    </div>
  );
}
